<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="querySearch || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" @click="$router.push('/category')"/>
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <div class="sort-btns">
      <div name="zh"  ref="zh" class="sort-item" @click="change($event)">综合</div>
      <div name="goods_sales"  ref="goods_sales" class="sort-item" @click="change($event)">销量</div>
      <div name="goods_price_max" ref="goods_price_max" class="sort-item" @click="change($event)">价格 </div>
    </div>

    <div class="goods-list">
      <GoodsItem v-for="item in proList" :key="item.goods_image" :item="item"></GoodsItem>
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/prodcut'
export default {
  name: 'SearchIndex',
  components: {
    GoodsItem
  },
  data () {
    return {
      page: 1,
      proList: [],
      proList1: [],
      value: 'zh'
    }
  },
  computed: {
    querySearch () {
      return this.$route.query.search
    }
  },
  async created () {
    const { data: { list } } = await getProList({
      categoryId: this.$route.query.categoryId,
      goodsName: this.querySearch,
      page: this.page
    })
    this.proList = list.data
    this.proList1 = [...this.proList]
    this.$refs.zh.style.color = 'red'
  },
  methods: {
    change (event) {
      this.value = event.target.getAttribute('name')
    }
  },
  watch: {
    // 监听变化
    value (newValue, oldValue) {
      if (newValue === 'goods_sales') {
        this.$refs.zh.style.color = ''
        this.$refs.goods_price_max.style.color = ''
        this.$refs.goods_sales.style.color = 'red'
        // 计算属性，根据proList中的属性进行排序
        // 使用数组的sort方法对proList进行排序
        this.proList = this.proList.slice().sort((a, b) => {
          // 根据属性的值进行排序，这里使用了三元运算符来实现升序或降序
          return b.goods_sales - a.goods_sales
        })
      }
      if (newValue === 'goods_price_max') {
        this.$refs.zh.style.color = ''
        this.$refs.goods_price_max.style.color = 'red'
        this.$refs.goods_sales.style.color = ''
        // 计算属性，根据proList中的属性进行排序
        // 使用数组的sort方法对proList进行排序
        this.proList = this.proList.slice().sort((a, b) => {
        // 根据属性的值进行排序，这里使用了三元运算符来实现升序或降序
          return a.goods_sales - b.goods_sales
        })
      }
      if (newValue === 'zh') {
        this.$refs.zh.style.color = 'red'
        this.$refs.goods_price_max.style.color = ''
        this.$refs.goods_sales.style.color = ''
        this.proList = this.proList1
      }
    }
  }

}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
